'use client';
import { useEffect, useState, useCallback } from 'react';
import { DataService } from '@/utils/api';
import jsPDF from 'jspdf';
import autoTable from 'jspdf-autotable';

export default function Home() {
    const [pestData, setPestData] = useState<any[]>([]);
    const [loading, setLoading] = useState(true);

    const fetchData = useCallback(async () => {
        try {
            const data = await DataService.getPestDetections();
            setPestData(data);
        } catch (err) {
            console.error('Fetch error');
        } finally {
            setLoading(false);
        }
    }, []);

    useEffect(() => {
        fetchData();
    }, [fetchData]);

    const downloadPDF = () => {
        const doc = new jsPDF();
        doc.text("ExpelAI - Pest Activity Report", 14, 15);
        autoTable(doc, {
            head: [['Date', 'Pest Type', 'Count', 'Severity']],
            body: pestData.map(p => [
                new Date(p.createdAt).toLocaleDateString(),
                p.pestType,
                p.count,
                p.severity || 'Low'
            ]),
        });
        doc.save("ExpelAI_Report.pdf");
    };

    if (loading) return <div className="min-h-screen bg-[#020617] text-white p-10">Loading Data...</div>;

    return (
        <div className="min-h-screen bg-[#020617] text-slate-200 p-10 font-sans">
            <div className="max-w-5xl mx-auto">
                <header className="flex justify-between items-center mb-12 border-b border-slate-800 pb-6">
                    <h1 className="text-2xl font-black text-white tracking-tighter">EXPEL<span className="text-emerald-500">AI</span></h1>
                    <button
                        onClick={downloadPDF}
                        className="bg-emerald-600 hover:bg-emerald-500 text-white px-5 py-2 rounded-xl font-bold text-sm transition-all"
                    >
                        ðŸ“¥ DOWNLOAD PDF LOG
                    </button>
                </header>

                <section className="bg-slate-900/50 border border-slate-800 p-8 rounded-[2rem] shadow-2xl">
                    <h2 className="text-xl font-bold text-white mb-6">Recent Activity Log</h2>
                    <div className="space-y-4">
                        {[...pestData].reverse().map((report, index) => (
                            <div key={index} className="p-5 bg-slate-800/40 border border-slate-700/50 rounded-2xl flex justify-between items-center">
                                <div>
                                    <p className="text-white font-bold text-lg">{report.pestType}</p>
                                    <p className="text-xs text-slate-500">{new Date(report.createdAt).toLocaleString()}</p>
                                </div>
                                <div className="text-right">
                                    <p className="text-emerald-400 font-black text-2xl">{report.count}</p>
                                    <p className="text-[10px] text-slate-500 uppercase font-bold tracking-widest">Pests Detected</p>
                                </div>
                            </div>
                        ))}
                    </div>
                </section>
            </div>
        </div>
    );
}